.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #db345b;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spinner 5s linear infinite;
  margin: auto;
}
@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
